module.exports = "<section class=credits> <h2>C'est quoi ?</h2> <p>Le th de markov est un gnrateur de <a href=https://fr.wikipedia.org/wiki/Ha%C3%AFku>haikus</a> semi-alatoire bas sur les <a href=https://fr.wikipedia.org/wiki/Cha%C3%AEne_de_Markov>chaines de Markov</a>.</p> <p>Il fonctionne avec un texte, un corpus d'entre. Ce corpus est dcoup en mots ou en groupes de mots (selon <em>l'ordre du modle</em>), chaque mot ou groupe du texte est li au mot ou au groupe suivant</p> <p>Une fois le texte d'entre ainsi analys, on procde  un tirage alatoire de \"phrases\", o l'enchainement des mots dpend de l'enchainement des mots dans le texte source</p> <p>Chaque phrase gnre est ensuite analyse pour en dterminer le nombre de syllabes, si cel satisfait les rgles du <em>haiku</em> (5 ou 7 syllabes, en une squence 5/7/5), le vers est conserv, sinon on recommence le tirage alatoire.</p> <h3>Astuces</h3> <p>De faon gnrale, plus le corpus d'entre sera volumineux, plus les vers du haiku seront ralistes / crdibles / intressants</p> <p>Attention, l'analyse d'un texte d'entre peut parfois tre longue et demander des ressources en processeur ou mmoire.</p> <p>Il peut tre intressant de mlanger plusieurs textes aux styles et aux vocabulaires diffrents  mais si vous voulez un vrai \"mlange\" des textes, il faudra que ceux-ci possdent des mots en commun.</p><p> </p><p>L'algorithme de comptage de syllabes franaises n'est pas parfait (c'est un authentique \"problme difficile\"), mettez les loups sur le compte de la licence potique :) </p> <h2>Crdits:</h2> <p><a href=https://github.com/substack/node-markov>Chaines de markov par substack</a></p> <p><a href=https://github.com/WhiteFangs/PoetifyJS>Algorithme de comptage de syllabes (fr) par WhiteFangs</a></p> <p><a href=\"https://thenounproject.com/Stephanie%20Wauters/uploads/?i=10676\">Thire inspire de Stephanie Wauters from the Noun Project</a></p> <p>Front-end : <a href=http://vuejs.org/ >VueJS</a> + webpack, etc.</p> <h2>Pour la suite ?</h2> <p>Faire une version anglophone ? </p> <p>De meilleurs tests ?</p> </section>";


